.cool-meals-title {
  background: #dc3e43;
  background-color: #dc3e43;
  background: conic-gradient(
    #dc3e43 20%,
    #e9740e 20%,
    #e9740e 40%,
    #d2e367 40%,
    #d2e367 60%,
    #4cbf5d 60%,
    #4cbf5d 80%,
    #5383e9 80%,
    #5383e9 100%
  );
  background-size: 100%;
  background-repeat: repeat;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  -webkit-animation: coolMealsHoverRev 0.5s ease forwards;
          animation: coolMealsHoverRev 0.5s ease forwards;
  font-family: "Bebas Neue", sans-serif;
  margin-left: 2rem;
  margin-top: 0;
  margin-bottom: 0;
}

.cool-meals-title:hover {
  animation: coolMealsHover 1s forwards;
  -webkit-animation: coolMealsHover 1s forwards;
  -moz-animation: coolMealsHover 1s forwards;
  -o-animation: coolMealsHover 1s forwards;
}

@-webkit-keyframes coolMealsHover {
  0% {
    background-size: 100%;
    background-position: 0 0;
  }
  20% {
    background-size: 100%;
    background-position: 0 1em;
  }
  100% {
    background-size: 300%;
    background-position: -4em 1em;
  }
}

@keyframes coolMealsHover {
  0% {
    background-size: 100%;
    background-position: 0 0;
  }
  20% {
    background-size: 100%;
    background-position: 0 1em;
  }
  100% {
    background-size: 300%;
    background-position: -4em 1em;
  }
}

@-webkit-keyframes coolMealsHoverRev {
  0% {
    background-size: 300%;
    background-position: -4em 1em;
  }
  20% {
    background-size: 100%;
    background-position: 0 1em;
  }
  100% {
    background-size: 100%;
    background-position: 0 0;
  }
}

@keyframes coolMealsHoverRev {
  0% {
    background-size: 300%;
    background-position: -4em 1em;
  }
  20% {
    background-size: 100%;
    background-position: 0 1em;
  }
  100% {
    background-size: 100%;
    background-position: 0 0;
  }
}

